import { layerSettings } from '.';

export function ClosedRecreationSitesLayerConfig(ls: layerSettings) {
    return [
        {
            type: 'esri-feature',
            id: 'closed-recreation-sites',
            title: 'Closed Recreation Sites',
            attribution: 'Copyright 117 DataBC, Government of British Columbia',
            popupTemplate: '<div class="smk-popup"><div class="popup-header"><span>Closed Recreation Site</span></div><div class="popup-title">{{feature.properties["PROJECT_NAME"]}}</div><div class="popup-attributes"><div class="label">Location:</div><div class="attribute">{{feature.properties["SITE_LOCATION"]}}</div></div><div class="popup-attributes"><div class="label">Closure Date:</div><div class="attribute">{{new Date(feature.properties["CLOSURE_DATE"]).toISOString().slice(0, 10)}}</div></div><div class="popup-attributes"><div class="label">Comment:</div><div class="attribute">{{feature.properties["CLOSURE_COMMENT"]}}</div></div><div class="popup-button-container"><a class="popup-button" target="_blank" rel="noopener"v-bind:href="`http://www.sitesandtrailsbc.ca/search/search-result.aspx?site=${feature.properties[\'FOREST_FILE_ID\']}&type=${feature.properties[\'PROJECT_TYPE\'] === \'SIT - Recreation Site\' ? \'SITE\' : \'TRAIL\'}`">Learn More</a></div></div>',

            attributes: [
                {
                    name: 'PROJECT_NAME',
                    title: 'Name'
                },
                {
                  name: 'FOREST_FILE_ID',
                  title: 'ID'
                },
                {
                  name: 'PROJECT_TYPE',
                  title: 'Type'
                },
                {
                    name: 'SITE_LOCATION',
                    title: 'Site Location'
                },
                {
                    name: 'SITE_DESCRIPTION',
                    title: 'Site Description'
                },
                {
                    name: 'CLOSURE_IND',
                    title: 'Closure Indicator'
                },
                {
                    name: 'CLOSURE_DATE',
                    title: 'Closure Date',
                },
                {
                    name: 'CLOSURE_TYPE',
                    title: 'Closure Type'
                },
                {
                    name: 'CLOSURE_COMMENT',
                    title: 'Closure Comments'
                }
            ],
            serviceUrl: 'https://services6.arcgis.com/ubm4tcTYICKBpist/ArcGIS/rest/services/RecSitesReservesInterpForests_DetailsClosures_publicView/FeatureServer/0',
            where: 'CLOSURE_IND = \'Y\'',
            titleAttribute: 'PROJECT_NAME',
            drawingInfo: {
                renderer: {
                    type: 'uniqueValue',
                    field1: 'CLOSURE_IND',
                    defaultSymbol: null,
                    uniqueValueInfos: [
                        {
                            value: 'Y',
                            symbol: {
                                angle: 0,
                                xoffset: 0,
                                yoffset: 0,
                                type: 'esriPMS',
                                url: 'c99abdd1-24c7-47d8-b714-4ad35866dd99',
                                width: 6.75,
                                height: 8.620481927710843,
                                imageData: '',
                                contentType: 'image/png'
                            },
                            label: 'Closed'
                        }
                    ]
                },
                transparency: 0
            }
        }
    ];
}
